</template>
<script>
export default {
  data() {
    return {
      startLocation: {
        lat: 6.0407546797993,
        lng: 4.5873869105892,
      },
      coordinates: {
        0: {
          full_name: "Engineering Office - Lagos",
          lat: "6.436460",
          lng: "3.472100",
        },
        1: {
          full_name: "Engineering Office - Warri",
          lat: "5.545740",
          lng: "5.763660",
        },
        2: {
          full_name: "Fabrication Yard & Marine Base - Warri",
          lat: "5.523330",
          lng: "5.788380",
        },
      },
      infoPosition: null,
      infoContent: null,
      infoOpened: false,
      infoCurrentKey: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35,
        },
      },
    };
  },
  methods: {
    getPosition: function (marker) {
      return {
        lat: parseFloat(marker.lat),
        lng: parseFloat(marker.lng),
      };
    },
    toggleInfo: function (marker, key) {
      this.infoPosition = this.getPosition(marker);
      this.infoContent = marker.full_name;
      if (this.infoCurrentKey == key) {
        this.infoOpened = !this.infoOpened;
      } else {
        this.infoOpened = true;
        this.infoCurrentKey = key;
      }
    },
  },
  mounted() {
    // this.$refs.mapRef.$mapPromise.then((map) => {
    //   map.panTo({ lat: 6.43646, lng: 3.4721 });
    // });
  },
};
</script>
